.profile__info {
    max-width: 383px;
    display: flex;
    flex-wrap: wrap;
    align-items: baseline;
    margin-left: 30px;
}

@media screen and (max-width: 767px) {
    .profile__info {
        max-width: 248px;
        margin: 14px 0 36px;
        justify-content: center;
    }
}