.elements__item {
    display: grid;
    grid-template-columns: repeat(auto-fit, 282px);
    grid-gap: 18px;
    justify-content: center;
    margin: 0;
    padding: 0;
}

@media screen and (max-width: 767px) {
    .elements__item {
        grid-template-columns: 1fr;
        row-gap: 20px;
    }
}